import CoworkingItem from "./CoworkingItem/CoworkingItem";
import styles from "./Coworking.module.scss";
import quality from "../../images/quality.svg";
import temp from "../../images/temp.svg";
import design from "../../images/design.svg";
import auto from "../../images/auto.svg";
import fast from "../../images/fast.svg";
import calendar from "../../images/calendar.svg";
import basket from "../../images/basket.svg";
import complect from "../../images/complect.svg";
import { AnimationOnScroll } from 'react-animation-on-scroll';

const Coworking = () => {
    return (
        <div className={styles.coworking}>
            <div className={styles.title}>
                      
            </div>
            <AnimationOnScroll animateIn="animate__fadeInLeft" animateOnce>
                <div className={styles.coworkingWrapper}>
                    <CoworkingItem src={fast} alt={'clock'} text={"    -  14 "} />
                    <CoworkingItem src={calendar} alt={'guarantee'} text={"        180 "} />
                </div>
            </AnimationOnScroll>
            <AnimationOnScroll animateIn="animate__fadeInRight" animateOnce>
                <div className={styles.coworkingWrapper}>
                    <CoworkingItem src={basket} alt={'clock'} text={"   "} />
                    <CoworkingItem src={quality} alt={'guarantee'} text={"    "} />
                </div>
            </AnimationOnScroll>
            <AnimationOnScroll animateIn="animate__fadeInLeft" animateOnce>
                <div className={styles.coworkingWrapper}>
                    <CoworkingItem src={temp} alt={'temp'} text={"       "} />
                    <CoworkingItem src={design} alt={'design'} text={"  "} />
                </div>
            </AnimationOnScroll>
            <AnimationOnScroll animateIn="animate__fadeInRight" animateOnce>
                <div className={styles.coworkingWrapper}>
                    <CoworkingItem src={auto} alt={'auto'} text={"          "} />
                    <CoworkingItem src={complect} alt={'set'} text={" ,      "} />
                </div>
            </AnimationOnScroll>
        </div>
    )
}

export default Coworking;